<template>
  <div>
    <div v-if="!loaded" class="loading">
      Loading board for <i>{{ username }}</i
      >...
    </div>
    <div class="board" v-if="loaded">
      <div v-if="collection.length > 0">
        <div v-if="showRandomGame" class="mb">
          <Game class="game" :game="randomLeastPlayed"></Game>
        </div>

        <div class="flex mb">
          <button type="button" @click="reroll">Random game</button>
          <div class="filter">
            <input
              v-model.trim="filter"
              autocorrect="off"
              autocapitalize="off"
              spellcheck="false"
              placeholder="Filter"
            />
          </div>
          <div class="select">
            <select v-model="sortBy">
              <optgroup
                v-for="(group, groupKey) in sortOptions"
                :key="groupKey"
                :label="group.name"
              >
                <option
                  v-for="(sortOption, key) in group.sortOptions"
                  :key="key"
                  :value="sortOption"
                >
                  {{ sortOption.name }}
                </option>
              </optgroup>
            </select>
          </div>
        </div>

        <div class="shelf">
          <Game
            class="game"
            v-for="game in collectionSorted"
            :key="game.id"
            :game="game"
          ></Game>
        </div>
      </div>
      <div v-else>
        <h1>Whoops!</h1>
        <p>You don't have any games in your collection.</p>
      </div>
    </div>
  </div>
</template>

<script>
import Request from "../modules/request.js";
import Game from "./Game.vue";

const sortByName = function (a, b) {
  return a.name.localeCompare(b.name);
};
const sortByDate = function (a, b, invertNull) {
  if (
    (a.lastPlayAgo === null && b.lastPlayAgo === null) ||
    a.lastPlayAgo === b.lastPlayAgo
  ) {
    return 0;
  } else if (a.lastPlayAgo === null) {
    return invertNull ? -1 : 1;
  } else if (b.lastPlayAgo === null) {
    return invertNull ? 1 : -1;
  }
  return a.lastPlayAgo <= b.lastPlayAgo ? -1 : 1;
};
const sortByPlays = function (a, b) {
  if (
    (a.playCount === null && b.playCount === null) ||
    a.playCount === b.playCount
  ) {
    return 0;
  } else if (a.playCount === null) {
    return 1;
  } else if (b.playCount === null) {
    return -1;
  }
  return a.playCount >= b.playCount ? -1 : 1;
};

const sortOptions = [
  {
    name: "by date",
    sortOptions: [
      {
        name: "most recent",
        sort(a, b) {
          const sort = sortByDate(a, b);
          return sort === 0 ? sortByName(a, b) : sort;
        },
      },
      {
        name: "least recent",
        sort(a, b) {
          const sort = sortByDate(a, b, true) * -1;
          return sort === 0 ? sortByName(a, b) : sort;
        },
      },
    ],
  },
  {
    name: "by plays",
    sortOptions: [
      {
        name: "most plays",
        sort: (a, b) => {
          const sort = sortByPlays(a, b);
          return sort === 0 ? sortByName(a, b) : sort;
        },
      },
      {
        name: "least plays",
        sort: (a, b) => {
          const sort = sortByPlays(a, b) * -1;
          return sort === 0 ? sortByName(a, b) : sort;
        },
      },
    ],
  },
];

export default {
  name: "Board",
  components: {
    Game,
  },
  props: {
    username: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      loaded: false,
      collection: null,
      filter: null,
      sortOptions,
      sortBy: sortOptions[0].sortOptions[0],
      showRandomGame: false,
      randomLeastPlayed: null,
    };
  },
  mounted() {
    Request.fetch(this.username).then((collection) => {
      this.collection = collection;
      this.loaded = true;
    });
  },
  methods: {
    pickLeastPlayed() {
      // let min = 0;
      // this.collection.reduce((min, game) => {
      //   if (game.playCount < min) {
      //     min = game.playCount;
      //   }
      // }, min);

      // let collection = this.collection.filter((game) => {
      //   if (game.playCount === min) {
      //     return true;
      //   }
      //   return false;
      // });
      let collection = this.collection;
      let randomGame = null;
      do {
        randomGame = collection[Math.floor(Math.random() * collection.length)];
      } while (this.randomLeastPlayed === randomGame && collection.length > 1);

      this.randomLeastPlayed = randomGame;
    },
    reroll() {
      this.showRandomGame = true;
      this.pickLeastPlayed();
    },
  },
  computed: {
    collectionSorted() {
      return this.collection
        .slice()
        .filter((game) => {
          return this.filter
            ? game.name.toLowerCase().includes(this.filter.toLowerCase())
            : true;
        })
        .sort(this.sortBy.sort);
    },
  },
};
</script>

